import React from "react";
import { Chord } from "tonal";
import { toAbc } from "tonal-abc-notation";
import player from "../../player";
import NameList from "../pcset/NameList";
const byNotes = num =>
  Chord.names()
    .filter(name => Chord.intervals(name).length === num)
    .sort();
const TRIADS = byNotes(3);
const CUATRIADS = byNotes(4);
export default ({ tonic }) => (
  <div className="NoteScales">
    <h2>Three note chords</h2>
    <NameList
      type="chord"
      route={(tonic, name) => [tonic, "chord", name]}
      tonic={tonic}
      names={TRIADS}
    />
    <h2>Four note chords</h2>
    <NameList
      type="chord"
      route={(tonic, name) => [tonic, "chord", name]}
      tonic={tonic}
      names={CUATRIADS}
    />
  </div>
);



