import React from "react";
import { Note } from "tonal";
import { toAbc } from "tonal-abc-notation";
import player from "../../player";
import NameTable from "../pcset/NameTable";

const COMMON = [
  "major",
  "minor",
  "harmonic minor",
  "melodic minor",
  "major pentatonic",
  "minor pentatonic",
  "dorian",
  "whole tone"
];
export default ({ tonic }) => (
  <div className="NoteScales">
    <h2>Common scales</h2>
    <NameTable
      type="scale"
      route={(tonic, name) => [tonic, "scale", name]}
      tonic={tonic}
      names={COMMON}
    />

  </div>
);



