import React from "react";
import * as Key from "tonal-key";
import Selector from "../shared/Selector";

const route = (keyName) => {
  const { tonic, mode } = Key.props(keyName)
  return [tonic, "key", mode]
}

export default ({ keyName }) => {
  const { tonic } = Key.props(keyName);
  const relatives = Key.modeNames().map(name => Key.relative(name, keyName));
  const paralells = Key.modeNames().map(name => tonic + " " + name);

  return (
    <div className="RelatedKeys">
      <h2>{keyName} related keys</h2>
      <Selector label="Relative modes" items={relatives} route={route} />
      <Selector label="Parallel modes" items={paralells} route={route} />
    </div>
  );
};



